import { PageScaffold } from "@/components/page-scaffold";

export default function ProfessionalDashboardPage() {
  return (
    <PageScaffold
      code="PRO-01"
      title="Dashboard CRM"
      objective="Visão operacional diária com KPIs, lista de pacientes e alertas de risco."
      primaryAction={{ label: "Novo paciente", href: "/patients/new" }}
      sections={[
        "KPIs (ativos, risco, pendentes review, adesão)",
        "Busca/filtros/ordenação",
        "Tabela de pacientes",
        "Alertas de risco",
      ]}
    />
  );
}
